purple, 10%);\n  color: white;\n  font-weight: 500;\n  padding: $s-size;\n}\n\n.button--link {\n  background: none;\n  border: none;\n  color: $off-white;\n  padding: 0;\n}\n",".container {\n  max-width: 60rem;\n  margin: 0 auto;\n  padding: 0 $m-size;\n}\n",".header {\n  background: $off-black;\n  color: white;\n  margin-bottom: $m-size;\n  padding: $m-size 0;\n}\n\n.header__title {\n  font-size: $l-size;\n  margin: 0;\n}\n\n.header__subtitle {\n  color: $off-white;\n  font-size: $m-size;\n  font-weight: 500;\n  margin: 0;\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .header {\n    margin-bottom: $xl-size;\n  }\n}\n",".ReactModalPortal>div {\n  opacity: 0;\n}\n\n.ReactModalPortal .ReactModal__Overlay {\n  align-items: center;\n  display: flex;\n  justify-content: center;\n  transition: opacity 200ms ease-in-out;\n}\n\n.ReactModalPortal .ReactModal__Overlay--after-open {\n  opacity: 1;\n}\n\n.ReactModalPortal .ReactModal__Overlay--before-close {\n  opacity: 0;\n}\n\n.modal {\n  background: $light-blue;\n  color: white;\n  max-width: 30rem;\n  outline: none;\n  padding: $l-size;\n  text-align: center;\n}\n\n.modal__title {\n  margin: 0 0 $m-size 0;\n}\n\n.modal__body {\n  font-size: 2rem;\n  font-weight: 300;\n  margin: 0 0 $l-size 0;\n  word-break: break-all;\n}\n",".option {\n  border-bottom: 1px solid lighten($light-blue, 10%);\n  display: flex;\n  justify-content: space-between;\n  padding: $l-size $m-size;\n}\n\n.option__text {\n  color: white;\n  font-weight: 500;\n  font-size: 2rem;\n  margin: 0;\n  word-break: break-all;\n}\n","// Widget\n.widget {\n  background: $light-blue;\n  margin-bottom: $xl-size;\n}\n\n.widget__message {\n  border-bottom: 1px solid lighten($light-blue, 10%);\n  color: $off-white;\n  margin: 0;\n  padding: $l-size;\n  text-align: center;\n}\n\n// Widget Header\n.widget-header {\n  background: $blue;\n  color: $off-white;\n  display: flex;\n  justify-content: space-between;\n  padding: $m-size;\n}\n\n.widget-header__title {\n  margin: 0;\n}\n"]}]);
